* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: #e5e3c9;
  background-image: url("https://transparenttextures.com/patterns/axiom-pattern.png");
  padding: 0 16px;
}

header {
  background-color: #789395;
  margin: auto -16px;
  padding: 16px 32px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
}

header h1 {
  color: #fff;
  font-family: 'Bree Serif', serif;
  font-weight: 200;
  text-align: center;
}

footer {
  position: relative;
  text-align: center;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}

footer p {
  color: #fff;
}

form {
  position: relative;
  width: 480px;
  margin: 3% auto 3% auto;
  background: #fff;
  padding: 15px;
  border-radius: 7px;
  box-shadow: 0 1px 5px rgb(138, 137, 137);
}

form input {
    width: 100%;
    border: none;
    padding: 4px;
    outline: none;
    font-size: 1.4rem;
    font-family: inherit;
    resize: none;
    text-align: center;
    font-family:'Segoe UI',
    Tahoma,
    Geneva,
    Verdana,
    sans-serif;
}

.synonyms {
  position: relative;
  width: 600px;
  margin: 3% auto 3% auto;
  text-align: center;
  list-style-position: inside;
  padding: 7%;
  background-color: rgba(120, 147, 149, 0.6);
  border-style: solid;
  border-radius: 5%;
  border-color: transparent;
}

@media only screen and (max-width: 600px) {
    .synonyms {
      position: relative;
      width: 100%;
      margin: 3% auto 3% auto;
      text-align: center;
      list-style-position: inside;
      padding: 7%;
      background-color: rgba(120, 147, 149, 0.6);
      border-style: solid;
      border-radius: 5%;
      border-color: transparent;
    }
    form {
      position: relative;
      width: 100%;
      margin: 3% auto 3% auto;
      background: #fff;
      padding: 15px;
      border-radius: 7px;
      box-shadow: 0 1px 5px rgb(138, 137, 137);
    }
  }

ul {
  font-size: 1.5rem;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #fff;
}